.boton-agregar {
    padding: 10px;
    background-color: #03a9f4;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.15s ease-in;
    border-radius: 3px;
    -webkit-box-shadow: 0 0.125rem 0.625rem rgb(22 170 255 / 33%), 0 0.0625rem 0.125rem rgb(22 170 255 / 50%);
}

.boton-agregar:hover {
    background-color: #039be5;
}

.boton-agregar > svg {
    color: white;
    font-size: 19px;
}

.form-control {
    background-color: #f9f9f969;
    border-radius: 4px !important;
    margin-right: 8px;
    border-bottom: 1px solid #e4e4e4;
  }